/* IMPORTS */
@import "includes/reset";
@import "includes/normalize";
@import "includes/variables";
@import "includes/mixins";
@import "grid/grid";
@import "grid/responsive";
@import "account/account";
@import "account/responsive";
@import "admin/admin";
@import "admin/responsive";
@import "store/store";
@import "store/responsive";
@import "includes/buttons";
@import "includes/responsive";

// Bootstrap
@import "../../../node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

/* GENERAL */
body {
    font-family: "OpenSans-Regular", sans-serif;
    background-color: $color_white;
    line-height: 1.2;
}

main {
    margin: 0 auto;
    padding-bottom: 50px;
    min-height: 1000px;
}

a {
    color: $color_black;
    &:hover, &:focus {
        color: $color_red;
        text-decoration: none;
    }
}

input[type=number] {
    -moz-appearance:textfield;
}
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
    margin: 0;
    -webkit-appearance: none;
}

/* HELPERS */
.text-center {
    text-align: center !important;
}

.cf:before,
.cf:after {
    content: "";
    display: table;
}
.cf:after {
    clear: both;
}
.cf {
    zoom: 1; /* ie 6/7 */
}

.no-wrap {
    white-space: nowrap;
}

table {
    .col-xs {
        width: 50px;
    }
    .col-sm {
        width: 80px;
    }
    .col-md {
        width: 155px;
    }
}

#wrapper {
    position: relative;
    margin: auto;
    max-width: 1920px;
}

.container-inner {
    margin: auto;
    padding: 0 5px;
    max-width: 1200px;
}

/* PAGINATION */
.product-pagination {
    .pagination {
        margin: 22px 10px;
        float: right;
        a, a:hover {
            color: $color_black;
        }
        .page-first, .page-last {
            font-weight: bold;
        }
        > li {
            display: inline-block;
            float: left;
        }
    }
}

/* HEADER */
#nav-top {
    background-color: $color_darkGray;
    border: 1px solid $color_brownBlack;
    border-radius: 0;
    a {
        color: $color_fontWhite;
    }
    a:hover {
        color: $color_red;
    }
    li:hover {
        background-color: #272323;
    }
    li.dropdown.open a.dropdown-toggle {
        background-color: $color_red;
    }
    li.dropdown > a {
        color: $color_fontWhite;
    }
    ul.dropdown-menu {
        background-color: $color_red;
        border-top: 1px solid $color_red;
        border-radius: 0;
        li.divider {
            background-color: $color_black;
        }
        li > a:hover, li > a:focus {
            background-color: $color_orange;
            color: $color_fontWhite;
        }
    }
    .navbar-brand {
        padding: 0 15px;
    }
}

.flash-message {
    margin: 10px auto;
    width: 50%;
    .message {
        display: inline-block;
        position: relative;
        padding: 20px 40px;
        width: 100%;
    }
    .message-success {
        color: #3c763d;
        background-color: #dff0d8;
        border: 1px solid #3c763d;
    } .message-info {
        color: #31708f;
        background-color: #d9edf7;
        border: 1px solid #31708f;
    } .message-warning {
        color: $color_orange;
        background-color: #fff3eb;
        border: 1px solid $color_orange;
    } .message-danger {
        color: $color_red;
        background-color: #ffe7e7;
        border: 1px solid $color_red;
    }
    .message-text {
        word-wrap: break-word;
    }
    .message-close {
        display: none;
        position: absolute;
        top: 10px;
        right: 10px;
        a, a:hover {
            color: $color_darkGray;
            opacity: 0.5;
        }
        a:hover {
            opacity: 0.8;
        }
    }
}

/* FOOTER */
footer {
    position: relative;
    bottom: 0;
    width: 100%;
    height: 50px;
    text-align: center;
    background-color: $color_black;
    clear: both;
    p {
        padding: 15px 0 0 0;
        margin: 0;
        color: #999999;
        font-size: 1em;
    }
}